export default "/* Hide the Mattermost name, icon and free label tag from the home page */\n[class^=\"ProductBrandingTeamEditionContainer\"] {\n\tdisplay: none !important;\n}\n\n/* Hide the Mattermost name, icon and free label tag from the login page */\n.header-logo-link:has(> .freeBadge) {\n\tdisplay: none !important;\n}\n\n/* Hide the team edition warning in the bottom of the open top left corner menu */\n#startTrial {\n\tdisplay: none !important;\n}\n\n/* Hide the leftover menu divider that was above the warning */\nli.menu-divider:has(+ #startTrial) {\n\tdisplay: none !important;\n}\n\n/* Hide the free trial banner / upgrade card in the admin console license page */\n.admin-console__banner_section:has(.AlertBanner.info.AlertBanner--sys) {\n\tdisplay: none !important;\n}\n\n/* Hide the TeamEditionRightPanel card in the right panel of the admin console license page */\n.right-panel:has(> .panel-card + .compare-plans-text) {\n\tdisplay: none !important;\n}\n\n/* Hide any upgrade dialogs that pop up*/\ndiv[role=\"dialog\"]:has(> #StartTrialFormModal) {\n\tdisplay: none !important;\n}\n\n/* Hide the footer notice text */\n.footer-copyright {\n\tdisplay: none !important;\n}\n\n/* Hide the view plans button in the top right corner of the dashboard */\n#RightControlsContainer #UpgradeButton {\n\tdisplay: none !important;\n}\n\n/* Hide the paid \"User Groups\" menu item in product switcher */\n#product-switcher-menu-dropdown #userGroups {\n\tdisplay: none !important;\n}\n\n/* Hide the premium setting to highlight keywords */\n#accountSettingsModal .section-min.isDisabled {\n\tdisplay: none !important;\n}\n"